<template>
  <div>
    <loading :active.sync="isLoading">
      <Circle4/>
    </loading>

    <!-- Navbar start -->
    <Navbar @changeLanguage="setLang"/>
    <!-- Navbar end -->

    <!-- Banner start -->
    <div class="product-banner bg-setting banner-img-filter"></div>
    <!-- Banner end -->

    <!-- Content start -->
    <section class="section">
      <div class="container">
        <div class="row">
          <div class="col-md-3 mobile-hide">
            <SideBar :isChangeLanguge = "changeLanguge"/>
          </div>
          <div class="col-md-9 col-sm-12">
            <Breadcrumb/>
            <router-view></router-view>
          </div>
        </div>
      </div>
    </section>
    <!-- Content end -->

    <!-- Footer start -->
    <Footer :isChangeLanguge = "changeLanguge"/>
    <!-- Footer end -->
  </div>
</template>
<script>
import Navbar from '../components/Navbar'
import Footer from '../components/Footer'
import SideBar from '../components/SideBar';
import Breadcrumb from '../components/Breadcrumb';
import { Circle4 } from 'vue-loading-spinner'

export default {
  name: 'Product',
  components:{
    Navbar,
    Footer,
    SideBar,
    Breadcrumb,
    Circle4
  },
  data(){
    return{
      isLoading: false,
      changeLanguge: true,
    }
  },
  methods:{
    // 儲存切換的語系
    setLang () {
      this.changeLanguge = !this.changeLanguge
    }
  },
  created(){
    this.isLoading = true
  },
  mounted(){
    this.isLoading = false
  }
}
</script>
<style lang="scss">
@import '../assets/scss/all';

.product-banner {
  width: 100%;
  height: 400px;
  background-image: url('../assets/images/productBanner.jpg');
  background-position: 0 80% !important;
}
.mobile-hide {
  @include screen($mobile){
    display: none;
  }
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>